import { Component } from '@angular/core';
import { NavController, ToastController, ModalController } from 'ionic-angular';
import { BLE } from '@ionic-native/ble';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { InterfazPage } from '../interfaz/interfaz';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  notificaciones: any[] = [];
  notificaciones2: any[] = [];
  disp = []
  
  scan;

  constructor(public navCtrl: NavController,private ble: BLE,
              public toastCtrl: ToastController,private localNotifications: LocalNotifications,
              public modalCtrl: ModalController) {

  }
    ScanBLE() {
      if(this.scan == true){
        this.MessageShow();
      }else{
        this.MessageShow();
        this.scan = true;
        this.disp.length = 0;
        this.ble.startScan([]).subscribe(device => {
        console.log(JSON.stringify(device));
        this.disp.push(device);
        });
        setTimeout(() => {
          this.ble.stopScan();
        }, 5000);
        this.scan = false;
      }
    }
    MessageShow(){
      let Message = this.toastCtrl.create({
      position: 'bot',
      message: 'Inicio el scaner',
      showCloseButton: true,
      closeButtonText: 'HAI'});
    }
    Interfaz(Id){
      let info = {
      ID: Id
    }
    this.modalCtrl.create(InterfazPage, info).present();
    }



    Noti(){
      let Noti1 = new Date();
      Noti1.setHours(19);
      Noti1.setMinutes(30);
      let Noti2 = new Date();
      Noti2.setHours(20);
      Noti2.setMinutes(30);

      let Noti001 = {
        id:1,
        title:'Tarea',
        text:'Esta es la tarea que encargo',
        add: Noti1
      };
      let Noti002 = {
        id:2,
        title:'Tarea2',
        text:'Esta es la tarea que encargo 2',
        add: Noti1
      };
      this.notificaciones.push(Noti001);
      this.notificaciones2.push(Noti002);
      
      console.log("Notificaciones: ",this.notificaciones);
      console.log("Notificaciones: ",this.notificaciones2);

      this.localNotifications.cancelAll().then(()=>{
        this.localNotifications.schedule(this.notificaciones);
        this.localNotifications.schedule(this.notificaciones2);
        this.notificaciones.length = 0;
        this.notificaciones2.length = 0;
        let teast = this.toastCtrl.create({
          position: 'top',
          message: 'Ya se hizo la notificacion',
          showCloseButton: true,
          closeButtonText: 'HAI'
        })
      });
    }
}



